"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { Car, FileText, Settings, Users } from "lucide-react"
import { cn } from "@/lib/utils"

export function AppSidebar() {
  const pathname = usePathname()

  const navItems = [
    { href: "/app", label: "Dashboard", icon: Car },
    { href: "/app/vehicles", label: "Vehicles", icon: Car },
    { href: "/app/taxes", label: "Taxes", icon: FileText },
    { href: "/app/admin/users", label: "Users", icon: Users },
  ]

  return (
    <aside className="w-64 border-r border-gray-200 bg-white flex flex-col">
      {/* Logo */}
      <Link href="/" className="h-16 border-b border-gray-200 flex items-center gap-3 px-6">
        <div className="w-8 h-8 bg-gray-900 rounded flex items-center justify-center">
          <span className="text-white font-bold text-sm">VM</span>
        </div>
        <span className="font-semibold text-gray-900">VehicleHub</span>
      </Link>

      {/* Nav Items */}
      <nav className="flex-1 px-4 py-6 space-y-2">
        {navItems.map((item) => {
          const Icon = item.icon
          const isActive = pathname === item.href
          return (
            <Link
              key={item.href}
              href={item.href}
              className={cn(
                "flex items-center gap-3 px-4 py-2 rounded-lg text-sm font-medium transition",
                isActive ? "bg-gray-100 text-gray-900" : "text-gray-600 hover:text-gray-900 hover:bg-gray-50",
              )}
            >
              <Icon size={18} />
              {item.label}
            </Link>
          )
        })}
      </nav>

      {/* Footer */}
      <div className="border-t border-gray-200 p-4">
        <Link
          href="#settings"
          className="flex items-center gap-3 px-4 py-2 rounded-lg text-sm font-medium text-gray-600 hover:text-gray-900 hover:bg-gray-50 transition"
        >
          <Settings size={18} />
          Settings
        </Link>
      </div>
    </aside>
  )
}
